import React, {useEffect, useMemo, useRef, useState} from 'react';
import ReactDOM from 'react-dom';

export {default as ModalHeader} from './modalHeader';
export {default as ModalBody} from './modalBody';
export {default as ModalFooter} from './modalFooter';

export interface IModalProps {
  className?: string;
  wrapperClassName?: string;
  onClose?: () => void;
  open?: boolean;
  renderAsPortal?: boolean;
  [others: string]: any;
}

const Modal: React.FC<IModalProps> = (props) => {
  const {className, onClose, children, open, renderAsPortal, wrapperClassName, ...rest} = props;
  const [position, setPosition] = useState<any>(undefined);
  const modalRef = useRef<HTMLDivElement>(null);

  const iconClose = (
    <button
      type="button"
      data-testid={rest && rest['data-testid'] ? `${rest['data-testid']}-icon-close` : undefined}
      className="modal-close-button"
      onClick={onClose}
    >
      <span className="material-icons">close</span>
    </button>
  );

  useEffect(() => {
    if (renderAsPortal && modalRef && modalRef.current && open) {
      const rect = modalRef.current.getBoundingClientRect();
      if (rect)
        setPosition({
          marginTop: `-${rect.height / 2}px`,
          marginLeft: `-${rect.width / 2}px`,
          width: `${rect.width}px`,
          height: `${rect.height}px`,
        });
    }
  }, [modalRef, modalRef.current, open]);

  const renderModal = () => (
    <div ref={modalRef} className={`modal ${className || ''}`} style={position} {...rest}>
      {typeof onClose === 'function' && iconClose}
      {children}
    </div>
  );
  const content = <div className={`tag-ds modal-wrapper ${wrapperClassName || ''}`}>{renderModal()}</div>;

  const container = document.getElementById('root') || document.body;
  return renderAsPortal ? (open ? ReactDOM.createPortal(content, container as Element) : null) : renderModal();
};

export default Modal;
